body {
  background: rgb(58, 58, 58);
  margin: 0;
}
#app {
  display: flex;
  flex-direction: column;
  height: 100vh;
  /* font-family: 'hanna'; */
  background-color: rgb(249,248,237);
}
.top {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 10vh;
  background-color: rgb(249,248,237);
}
.stx {
  font-size: 60px;
  color:rgb(253, 198, 2);
  font-weight: bold;
  text-shadow: 2px 2px 5px rgba(0, 0, 0, 0.6);
}
.letter {
  margin-right: 10px;
}
.main {
  flex: 1;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  padding: 20px;
}
.lnb {
  width: 20%;
}
.content {
  width: 75%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.call {
  box-shadow: 2px 2px 4px rgba(0, 0, 0, 0.4);
  width: 550px;
  height: 450px;
  border-radius: 50%;
  font-size: 48px;
  font-weight: bold;
  color: #282828;
  /* background-color: #FED770; */
  background-color: rgb(252,211,69);
  border: none;
  cursor: pointer;
  animation: bounce 2.5s infinite;
}
@keyframes slide {
0% {
    transform: translateX(0);
}
50% {
    transform: translateX(50px);
}
100% {
    transform: translateX(0);
}
}
@keyframes fade {
0% {
    opacity: 0;
}
100% {
    opacity: 1;
}
}
@keyframes rotate {
0% {
    transform: rotate(0);
}
100% {
    transform: rotate(360deg);
}
}
@keyframes bounce {
0%, 100% {
    transform: translateY(0);
}
50% {
    transform: translateY(-20px);
}
}
@keyframes pulse {
0% {
    transform: scale(1);
}
50% {
    transform: scale(1.05);
}
100% {
    transform: scale(1);
}
}




.modal {
    position: absolute;
    z-index: 10;
    top: 0;
    left: 0;
    background-color: (0, 0, 0);
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}
.email {
    background: rgb(1,129,220);
    box-shadow: 2px 2px 4px rgba(0, 0, 0, 0.4);
    /* font-family:'hanna'; */
    color: rgb(255, 255, 255);
    padding: 10px 20px;
    border-radius: 5px;
    font-size: 35px;
    font-weight: 600;
    border: 1px;
    width: 100%;
    height: 100px;
    cursor: pointer;
}



